import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FrontComponent } from './front.component';

import { NavMenuComponent } from './components/shared/navmenu/navmenu.component';
import { FooterComponent } from './components/shared/footer/footer.component';

@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: FrontComponent
            },
            {
                path: 'home',
                loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\home\\home.module')['HomeModule']);  });})
            },
            {
                path: 'blog',
                loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\blog\\blog.module')['BlogModule']);  });})
            },
            {
                path: 'blog-detail/:id',
                loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\blog-item\\item.module')['ItemModule']);  });})
            },
            {
                path: 'counter',
                loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\counter\\counter.module')['CounterModule']);  });})
            },
            {
                path: 'fetch-data',
                loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\fetchdata\\fetchdata.module')['FetchdataModule']);  });})
            },
        ])
    ],
    exports: [RouterModule],
    declarations: [
        FrontComponent,
        NavMenuComponent,
        FooterComponent
    ]
})

export class FrontModule { }   


